import React, { useCallback, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack, Button, RadioGroup, Radio
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import Icon from '../../../components/Icon';
import style from './MoveFundsAnotherBank.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import {
  fetchPlaidToken, linkPlaidAccount, listSelfBanks, removeSelfBank
} from '../../../store/middlewares/account';
import { apiErrorHandler, showNotification } from '../../../utils/general';
import OpenPlaidLink from '../../../components/Plaid';
import { useDrawer } from '../../../hooks/drawer';

const MoveFundsAnotherBankInfo = React.lazy(() => import('../MoveFundsAnotherBankInfo/MoveFundsAnotherBankInfo'));

const MoveFundsAnotherBank = () => {
  const { t } = useTranslation();
  const { open } = useDrawer();
  const dispatch = useAppAsyncDispatch();
  const { selectedAccount: account, selfBankAccounts } = useAppSelector((store) => store.account, shallowEqual);

  const [plaidToken, updatePlaidToken] = useState<string | null>(null);
  const [selectedContact, selectContact] = useState('');

  const getPlaidToken = useCallback(() => {
    if (account) {
      dispatch(fetchPlaidToken(account?.id))
        .then((data) => {
          updatePlaidToken(data.linkToken);
        })
        .catch(apiErrorHandler);
    }
  }, [account]);

  const plaidOnSuccess = useCallback(
    // eslint-disable-next-line camelcase
    (public_token: string, metadata: any) => {
      updatePlaidToken(null);
      if (account?.id) {
        dispatch(
          linkPlaidAccount({
            accountId: account.id,
            plaidAccountId: metadata.accounts[0]?.id,
            // eslint-disable-next-line camelcase
            plaidToken: public_token,
          })
        ).catch(() => {
          showNotification('An error occurred.', 'Unable to verify plaid account', 'error');
        });
      }
    },
    [account]
  );

  useEffect(() => {
    if (account) {
      dispatch(listSelfBanks(account.id));
    }
  }, [account]);

  return (
    <div className="page">
      <MiniBox
        heading={t('move_funds_another_bank.title')}
        footer={(
          <Button
            width="100%"
            variant="primary"
            disabled={selectedContact === ''}
            onClick={() => {
              open(MoveFundsAnotherBankInfo, {
                contactId: selectContact
              });
            }}
          >
            {t('move_funds_another_bank.ctaText')}
          </Button>
        )}
      >
        <Box width="100%">
          {selfBankAccounts.length !== 0 ? (
            <>
              <Text variant="large" className={style.pullFrom}>{t('move_funds_another_bank.list_title')}</Text>

              <Box className={style.bankList}>
                <RadioGroup onChange={selectContact} value={selectedContact}>
                  {selfBankAccounts.map((b) => (
                    <Box key={b.id} className={style.bankInfo}>
                      <Radio value={b.id}>
                        <Text variant="regular" className="text-dark">
                          {b.ach.bankName}
                          {' '}
                          XXXXXX
                          {' '}
                          {b.ach.accountNumber.slice(b.ach.accountNumber.length - 4)}
                        </Text>
                      </Radio>
                      <Text
                        className={style.removeBank}
                        onClick={() => {
                          dispatch(removeSelfBank(b.id));
                        }}
                      >
                        {t('move_funds_another_bank.remove')}

                      </Text>
                    </Box>
                  ))}
                </RadioGroup>

              </Box>

            </>
          ) : null }

          <Text variant="large" className={style.linkAccount}>{t('move_funds_another_bank.linkAccount')}</Text>

          <Button
            width="100%"
            variant="unstyled"
            onClick={() => {
              getPlaidToken();
            }}
          >
            <HStack className={style.transferType}>
              <Box>
                <Text variant="regular">{t('move_funds_another_bank.linkAnotherAccount')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>
          </Button>

          {plaidToken ? (
            <OpenPlaidLink
              token={plaidToken}
              onSuccess={plaidOnSuccess}
              onExit={() => {
                updatePlaidToken(null);
              }}
            />
          ) : null}

        </Box>
      </MiniBox>

    </div>
  );
};

export default MoveFundsAnotherBank;
