import { useEffect, FunctionComponent } from 'react';
import {
  usePlaidLink,
  PlaidLinkOptions,
  PlaidLinkOnSuccess,
  PlaidLinkOnExit
} from 'react-plaid-link';

interface Props {
  token: string;
  onSuccess: PlaidLinkOnSuccess;
  onExit: PlaidLinkOnExit
}

const OpenPlaidLink: FunctionComponent<Props> = ({ token, onSuccess, onExit }) => {
  const config: PlaidLinkOptions = {
    // When re-initializing Link after OAuth redirection, the same
    // Link token from the first initialization must be used
    token,
    onSuccess,
    // receivedRedirectUri: document.location.href, // required for OAuth
    onExit
    // onEvent
  };

  const { open, ready } = usePlaidLink(config);

  // this opens link as soon as it's ready
  useEffect(() => {
    if (!ready) {
      return;
    }
    // const plaidLinkIframe = document.getElementById('plaid-link-iframe-1')
    open();
  }, [ready, open]);

  // don't render anything, just open Link
  return null;
};

export default OpenPlaidLink;
